body {
    /* overflow-x: hidden; */
    background: #0a192f;
}

#aboutSection,
#projectSection,
#interestsSection,
#contactSection {
    height: 100vh;
    width: 100vw;
    position: relative;
    /* margin-left: 12vw; */
    /* margin-right: 12vw; */
}

.Navbar {
    min-width: 100%;
    min-height: 11vh;
    /* border: 2px solid white; */
    position: fixed;
    top: 0;
    background: #0a192f46;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    z-index: 1000;
    /* box-shadow: 0px 0px 10px black; */
}

.logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-shadow: 0px 0px 5px rgb(100, 255, 218);
}

.spanContainer:nth-child(1) {
    margin-left: 20px;
}

.spanContainer {
    margin-left: 5px;
    color: rgb(100, 255, 218);
    font-size: xx-large;
    font-family: Fira Code;
}

.ul_div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.ul_div>ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    flex-wrap: wrap;
}

.li_item {
    margin: 5px;
    color: rgb(204, 214, 246);
    font-family: Fira Code;
}

.liSpan1 {
    color: rgb(100, 255, 218);
    margin: 5px;
}

.aNav {
    text-decoration: none;
    color: rgb(204, 214, 246);
}

.ulNav {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:600px) {
    *{
        overflow-x: hidden;
    }

    .logo{
        margin-left: 60px;
    }
    .spanContainer{
        margin-left: 10px;
        margin-top: 10px;
    }
    
    .ul_div{
        margin-left: 40px;
    }
}