.sectionabout {
  /* margin-left: 12vw; */
  /* margin-right: 12vw; */
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  flex-wrap: wrap;
  min-height: 100vh;
  /* border: 2px solid blue;// */
  width: 500px;
  padding-left: 12vw;
  padding-right: 12vw;
}

.headingAbout {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-family: Fira Code;
  position: relative;
  margin-top: 14vh;
  left: -900px;
  padding: 0;
  z-index: -2;
}

.hrAbout{
  transform: rotate(0deg);
  width: 300px;
  height: 1px;
  margin-left: 50px;
  position: relative;
  top: 0px;
}

.pAbout {
  width: 700px;
  color: rgb(136, 146, 176);
  /* transition:0.5s; */
  margin-top: 10px;
  height: 300px;
  position: absolute;
  opacity: 0;
  transform: scale(0);
  background: #112441;
  box-shadow: inset -10px -10px 20px #070e1a, inset 10px 10px 20px #1b3a68;
  border-radius: 50px;
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}

p:hover {
  color: rgb(100, 255, 218);
}

.Saurabh{
  /* float: right; */
  width: 300px;
  height: 300px;
  filter: blur(2px);
  /* transition: 0.5s; */
  border-radius: 25px;
  cursor: pointer;
  position: absolute;
  right: -35vw;
  opacity: 0;
  margin: 10px;
  padding: 10px;
  box-shadow: inset -10px -10px 20px #070e1a, inset 10px 10px 20px #1b3a68;
}

.Saurabh:hover {
  transform: translateY(-10px) scale(1.2);
  filter: blur(0px);
  padding: 0;
  box-shadow: 0px 0px 10px rgb(100, 255, 218);
}

.h2about1 {
  color: rgb(100, 255, 218);
  font-size: 30px;
  margin-left: 50px;
}

.h2about2 {
  color: #ccd6f6;
  font-size: 40px;
  margin-left: 50px;
}

.bubbleAbout {
  position: absolute;
  margin-top: 14vh;
  top: 15px;
  right: 350px;
  background: #112441;
  box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
  margin-right: 8vw;
  width: 10vw;
  height: 20vh;
  border-radius: 50%;
  /* border: 2px solid blue; */
  outline: none;
  animation: bubble 2s linear infinite;
  z-index: -1;
  opacity: 0;
}

.bubbleAbout1 {
  position: absolute;
  margin-bottom: 14vh;
  bottom: -10px;
  left: 0;
  background: #112441;
  box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
  margin-left: 8vw;
  width: 10vw;
  height: 20vh;
  border-radius: 50%;
  /* border: 2px solid blue; */
  outline: none;
  animation: bubble 2s linear infinite;
  opacity: 0;
}

@keyframes bubble {
  0% {
    transform: translateY(0px);
  }
  25% {
    transform: translateY(25px);
  }
  50% {
    transform: translateY(50px);
  }
  75% {
    transform: translateY(25px);
  }
  100% {
    transform: translateY(0px);
  }
}

@media screen and (max-width:500px){
  .h2about1{
    font-size: 4vw;
    margin-left: 20px;
    margin-right: 0px;
    width: 100px;
    padding: 0;
    /* margin-top: 0; */
  }

  .sectionabout{
    margin-left: 0vw;
    margin-right: 0vw;
    padding: 0;
    margin-top: 14vh;
    min-width: 100vw;
    min-height: 100vh;
  }
  .h2about2{
    font-size:5vw;
    margin-left: 10px;
    width: 50vw;
    /* margin-top: 0; */
  }

  hr{
    position: absolute;
    width: 100px;
    margin-left: 0px;
    top: -14px;
  }

  .pAbout{
    width: 80vw;
    margin-left: 10vw;
  }

  .Saurabh{
    position: absolute;
    bottom: 2vw;
    /* right: -28vw; */
    width: 250px;
    height: 250px;
  }

  img:hover{
    z-index: 6;
  }

  .bubbleAbout{
    width: 100px;
    height: 100px;
    right: 0;
  }
  .Saurabh{
    width: 250px;
    height: 250px;
  }
  .bubbleAbout1{
    width: 100px;
    height: 100px;
    margin-bottom: 7vh;
  }
}

