.interestsSection {
    padding-top: 5vw;
    padding-left: 12vw;
    padding-right: 12vw;
    /* border: 2px solid blue; */
}

.headingInterests {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: Fira Code;
    position: relative;
    left: -900px;
    /* padding: 0; */
    z-index: -2;
}

.h6i1 {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    padding: 5vh;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    border-radius: 25px;
    width: 30vw;
    opacity: 0;
    position: absolute;
    margin-top: 0vh;
    margin-right: 0vw;
    font-size: 20px;
    left: -45vw;
    transform: scale(0);
    z-index:2;
}

