.bubbleInterest {
    position: absolute;
    margin-top: 14vh;
    top: 0;
    right: 0;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    margin-right: 8vw;
    width: 10vw;
    height: 20vh;
    border-radius: 50%;
    /* border: 2px solid blue; */
    outline: none;
    opacity: 0;
    animation: bubble 2s linear infinite;
}

.bubbleInterest1 {
    position: absolute;
    margin-bottom: 14vh;
    bottom: -10px;
    left: 0;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    margin-left: 8vw;
    width: 10vw;
    height: 20vh;
    border-radius: 50%;
    /* border: 2px solid blue; */
    outline: none;
    opacity: 0;
    animation: bubble 2s linear infinite;
}

@keyframes bubble {
    0% {
        transform: translateY(0px);
    }

    25% {
        transform: translateY(25px);
    }

    50% {
        transform: translateY(50px);
    }

    75% {
        transform: translateY(25px);
    }

    100% {
        transform: translateY(0px);
    }
}

.imgIntro{
    opacity: 1;
    margin: 0;
    /* width: 300px; */
    right: 100px;
    top: 150px;
    position: absolute;
    float: right;
    z-index: -1;
}

@media screen and (max-width:500px){
    .h6i1,.h6i2,.h6i3,.h6i4{
        min-width: 60vw;
        font-size: 15px;
        padding: 4vh;
    }
    
    .h6i2{
        margin-top: 19vh;
    }

    .h6i3{
        margin-top: 35vh;
    }

    .h6i4{
        margin-top: 50vh;
    }
}

@media screen and (max-width:700px){
    .interestsSection {
        margin-left: 0px;
        margin-right: 0px;
        padding-left: 0px;
        padding-right: 0px;
        padding-top: 0vh;
    }
    .h6i1,.h6i2,.h6i3,.h6i4{
        min-width: 50vw;
        min-height: auto;
        /* font-size: 15px; */
        /* left:-50vw; */
    }

    .h6i2{
        margin-top: 15vh;
    }

    .h6i3{
        margin-top: 30vh;
    }

    .h6i4{
        margin-top: 45vh;
    }

    .imgIntro{
        opacity: 1;
    margin: 0;
    width: 300px;
    right: 2vw;
    top: 150px;
    position: absolute;
    float: right;
    z-index: -1;
    display: none;
    }
}