.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  min-width: 100vw;
  min-height: 100vw;
}

.sidebar1{
  width: 12vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  /* background: #000; */
  /* float: left; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 100;
}
.ul{
  padding: 0;
  list-style: none;
  position: absolute;
}

.li{
  position: relative;
  color: rgb(204, 214, 246);
  font-size: 25px;
  margin: 40px;
  left: 5px;
  transition: 0.5s;
}

.li:hover{
  color: rgb(100, 255, 218);
  transform: scale(1.5);
}

.li2{
  position: relative;
  color: rgb(204, 214, 246);
  font-size: 20px;
  margin: 40px;
}
.sidebar2{
  width: 12vw;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  /* background: #000; */
  /* float: left; */
  z-index: 100;
}
.ulh1{
  transform: rotateZ(90deg);
  /* font-style: italic; */
  position: absolute;
  top: 255px;
  padding: 0;
  font-size: 15px;
  right: -173px;
}

a{
  text-decoration: none;
  color: rgb(204, 214, 246);
  transition: 0.5s;
}

a:hover{
  color: rgb(100, 255, 218);
}
::-webkit-scrollbar{
  width: 0;
}

*{
  scroll-behavior: smooth;
  box-sizing: border-box;
}

::selection{
  display: none;
  pointer-events: none;
  /* color: transparent; */
  text-shadow: 10px 10px 10px black;
}

.hr{
  /* transform-style: preserve-3d; */
  transform: rotate(90deg);
  width: 300px;
  height: 1px;
  position: absolute;
  left: -55px;
  bottom: 50px;
}

@media screen and (max-width:700px){
  .sidebar1,.sidebar2{
    display: none;
  }

  .App{
    overflow-x:hidden ;
    padding: 0vw;
  }
}