#contactSection{
    /* border: 2px solid blue; */
    padding-left: 12vw;
    padding-right:12vw;
}
*{
    font-family: Fira Code;
}
.heading{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: Fira Code;
}

hr{
    transform: rotate(0deg);
    width: 300px;
    height: 1px;
    margin-left: 50px;
    position: relative;
    top: 0px;
}

.contact{
    padding-top:50px;
    padding-left: 50px;
    display: flex;
    /* justify-content: center; */
    align-items: center;
}

.call{
    color: rgb(100, 255, 218);
    font-size: x-large;
    margin: 10px;
}

.call_me{
    color: aliceblue;
    font-size: x-large;
    margin: 10px;
    transition: 0.5s;
    text-decoration:none;
}

.call_me:hover {
    transform: translateX(10px);
    /* text-decoration: wavy; */
    /* text-decoration-style: wavy; */
    color:rgb(100, 255, 218);
    font-style: italic;
}

.number{
    font-size: 20px;
    color: aliceblue;
    transition: 0.5s;
    margin-left: 10px;
    /* text-decoration: underline; */
}

.number:hover{
    transform: translateX(50px) scaleX(1.2);
    color:rgb(100, 255, 218);
    font-style: italic;
}


