.headingProject {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-family: Fira Code;
    position: relative;
    left: -900px;
    /* padding: 0; */
    z-index: -2;
}

.h61 {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    padding: 5vh;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    border-radius: 25px;
    width: 30vw;
    opacity: 0;
    position: absolute;
    margin-top: 0vh;
    left: -44vw;
}

.h62 {
    display: flex;
    justify-content: space-between;
    margin: 20px;
    padding: 5vh;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    border-radius: 25px;
    width: 30vw;
    opacity: 0;
    position: absolute;
    margin-top: 20vh;
    left: -44vw;
}

.bubble {
    position: absolute;
    margin-top: 14vh;
    top: 0;
    right: 0;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    margin-right: 8vw;
    width: 10vw;
    height: 20vh;
    border-radius: 50%;
    /* border: 2px solid blue; */
    outline: none;
    opacity: 0;
    animation: bubble 2s linear infinite;
}

.bubble1 {
    position: absolute;
    margin-bottom: 14vh;
    bottom: -10px;
    left: 0;
    background: #112441;
    box-shadow: inset -22px -22px 43px #070e1a, inset 22px 22px 43px #1b3a68;
    margin-left: 8vw;
    width: 10vw;
    height: 20vh;
    border-radius: 50%;
    /* border: 2px solid blue; */
    outline: none;
    opacity: 0;
    animation: bubble 2s linear infinite;
}

@keyframes bubble {
    0% {
        transform: translateY(0px);
    }

    25% {
        transform: translateY(25px);
    }

    50% {
        transform: translateY(50px);
    }

    75% {
        transform: translateY(25px);
    }

    100% {
        transform: translateY(0px);
    }
}

.arrow {
    /* background: #000; */
    border-radius: 50%;
    box-shadow: inset 0px 0px 10px rgb(100, 255, 218), 0px 0px 10px black;
    position: absolute;
    width: 50px;
    height: 50px;
    padding: 5px;
    transform: rotate(0deg);
    margin-left: 40px;
    transform: translateY(-10px);
    font-size: 40px;
    color: rgb(100, 255, 218);
    animation: animate 2s linear infinite;
    opacity: 1;
    cursor: pointer;
    z-index: 10;
    animation: arrow 3s linear infinite;
}

@keyframes arrow {
    0%{
        transform:translateX(0px) rotate(0deg);
    }
    50%{
        transform:translateX(100px) rotate(90deg);
    }
    100%{
        transform:translateX(0px) rotate(360deg);
    }
}

.subsection1{
    position: absolute;
    top: 12vh;
    width: 76vw;
    height: 76vh;
    border-radius: 50px;
    border: 2px solid blue;
    background: #112441;
    box-shadow: inset -10px -10px 20px #070e1a, inset 10px 10px 20px #1b3a68, 0px 0px 50px black;
    opacity: 1;
    right: -500px;
    transition: 1s;
    opacity: 0;
    animation: animate1 2s linear infinite;
}

@keyframes animate1 {
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(0px);
    }
}

