        padding-right: 0px;
        padding-top: 14vh;
    }

    .h61,
    .h62 {
        font-size: 20px;
        width: 70vw;
        /* padding-right: 30vw; */
        margin: 0;
        margin-top: 10vh;
        left: -50vw;
    }

    .h62 {
        margin-top: 30vh;
    }

    .arrow {
        display: none;
    }

    .liSpan {
        margin-left: 20px;
    }

    .bubble {
        width: 100px;
        height: 100px;  
        right: 0;
    }

    .bubble1 {
        width: 100px;
        height: 100px;
        margin-bottom: 7vh;
    }
}