.section{
    margin-left: -8vw;
    /* margin-right: 12vw; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 72vw;
    height: 100vh;
    margin-top: 10vh;
}


.h1{
    font-family: Fira Code;
    color: rgb(100, 255, 218);
    font-size: 16px;
    font-weight: 400;
    display: block;
    text-shadow: 10px 10px 10px black;
}

.h2Intro{
    color: rgb(204, 214, 246);
    font-size: 80px;
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    text-shadow: 10px 10px 10px black;
}

.h2span1{
    margin-right: 15px;
}

.h2span2{
    margin-right: 15px;
}

.h2span3{
    margin-right: 15px;
    color: rgb(136, 146, 176);
}

.h2span4{
    color: rgb(136, 146, 176);
}

.p{
    width: 50vw;
    color: rgb(136, 146, 176);
}

.imgIntro1{
    width: 400px;
    position: absolute;
    right: 150px;
    z-index: -1;
}

@media screen and (max-width:500px) {
    .section{
        margin-top: 0vh;
        margin-right: 0vw;
        margin-right: 0vw;
    }

    .h2Intro{
        display: flex;
        flex-wrap: wrap;
        font-size: 8vw;
    }

    .h2span3,.h2span4{
        font-size:7vw;
    }
    .imgIntro1{
        position: absolute;
        left:68vw;
        top: 30vh;
        /* bottom: 0; */
        width: 30vw;

    }
    .p{
        width: 72vw;
    }
}

@media screen and (min-width:501px) and (max-width:700px) {
    .section{
        margin-top: 0vh;
        margin-right: 0vw;
        margin-right: 0vw;
    }

    .h2Intro{
        display: flex;
        flex-wrap: wrap;
        font-size: 10vw;
    }

    .h2span3,.h2span4{
        font-size:8vw;
    }
    .imgIntro1{
        position: absolute;
        left:25vw;
        top:80vh;
        /* bottom: 0; */
        width: 50vw;

    }
    .p{
        width: 72vw;
    }
}